\\psi\\rangle = \\cos{\\frac{\\theta}{2}}|0\\rangle + \\mathrm{e}^{i\\phi}\\sin{\\frac{\\theta}{2}}|1\\rangle$ given $\\theta\\in [0,\\pi]$ and $\\phi\\in [0,2\\pi]$. The parameters $\\theta$ and $\\phi$ can be visualized in spherical coordinates shown below, which is called a "),_c('a',{attrs:{"href":"https://en.wikipedia.org/wiki/Bloch_sphere","target":"_blank"}},[_vm._v("Bloch sphere")]),_vm._v(".")]),_c('div',{staticStyle:{"text-align":"center"}},[_c('img',{attrs:{"src":require("@/assets/Bloch_sphere.svg")}})]),_c('p',[_vm._v("We use this geometrical representation of single qubit in pure state throughout the project. That means, you can view the spheres in GUI as qubits in Bloch sphere.")]),_c('p',[_vm._v("Quantum operators can change the state of a qubit, just like classical gates. Analogous to classical NOT gate that maps 0 to 1 and 1 to 0, a Pauli X operator in quantum mechanics maps $|0\\rangle$ to $|1\\rangle$ and $|1\\rangle$ to $|0\\rangle$. Thus, Pauli X operator is also known as bit-flip operator that maps any state $\\alpha|0\\rangle+\\beta|1\\rangle$ to $\\alpha|1\\rangle+\\beta|0\\rangle$. On the contrary, Pauli Z operator does not have correspondence in classical circuit, which is only introduced in the existence of superposition. Pauli Z operator is also known as phase-flip operator because it changes the relative phase of $|1\\rangle$ to $\\mathrm{e}^{\\mathrm{i}\\pi}|1\\rangle=-|1\\rangle$, mapping any state $\\alpha|0\\rangle+\\beta|1\\rangle$ to $\\alpha|0\\rangle-\\beta|1\\rangle$. With Bloch sphere, one can visualize the effect of Pauli operators as rotating the state along its corresponding axis, for example, Pauli X operator rotates the state along X axis and Pauli Z operator rotates the state along Z axis with $\\pi$ angle (180$\\deg$). Similarly, Pauli Y operator rotates the state along Y axis, which is equivalent to first applying Z operator then X operator. We use this visualization in our GUI, see the interactive part below.")]),_c('el-card',{attrs:{"shadow":"always","body-style":{ padding: '10px 20px', background: '#2A5CAA' }}},[_c('p',{staticClass:"interactive-message"},[_vm._v("Interactive Part: customize single qubit errors by adding Pauli operators (Pauli errors)\n\t\t\t\t\t\t\t"),_c('el-button',{staticClass:"interactive-start",attrs:{"type":"primary","icon":_vm.running == 'single_qubit' ?  'el-icon-loading' : 'none',"disabled":_vm.running != null},on:{"click":function($event){return _vm.start_interactive('single_qubit')}}},[_vm._v(_vm._s(_vm.running == "single_qubit" ? "Running" : "Start"))])],1)]),_c('p',[_vm._v("Different from the classical world where measurement is non-destructive to the information, in quantum world measurement usually changes the quantum state by projecting it to a random base of the measurement operator. We can measure the qubit along Z axis, who has the same orthogonal bases $|0\\rangle$ and $|1\\rangle$ as the computational bases of a qubit. When measuring a qubit along Z axis, it will collapse to one of the orthogonal bases $|0\\rangle$ and $|1\\rangle$ with probability $|\\alpha|^2$ and $|\\beta|^2$ respectively. If the measurement result is +1, then after measurement the qubit is in $|0\\rangle$ state, otherwise the measurement result is -1 and the qubit is in $|1\\rangle$ state after measurement. The measurement of quantum state is destructive in general because it collapses the superposition state $\\alpha|0\\rangle+\\beta|1\\rangle$ to either $|0\\rangle$ or $|1\\rangle$, losing the information of their relative phase $\\phi$ and relative amplitude $\\tan{\\frac{\\theta}{2}}=|\\frac{\\beta}{\\alpha}|$.")])],1),_c('div',{directives:[{name:"show",rawName:"v-show",value:(_vm.step == 1),expression:"step == 1"}]},[_c('h1',[_vm._v("Quantum Computing")]),_c('p',[_vm._v("The exponentially faster quantum computing comes from the superposition of quantum states. If a quantum computer has $n$ qubits, then there are $2^n$ computational bases $|00..000\\rangle$, $|00..001\\rangle$, $|00..010\\rangle$, ..., $|11..111\\rangle$. Unlike a classical circuit with $n$ bits, a quantum circuit with $n$ qubits has $2^n$ bits information because each of its $2^n$ computational bases can be individually superposed. The essence of quantum computing is to use these exponentially larger computational bases to search the result in parallel. Quantum algorithms based on "),_c('a',{attrs:{"href":"https://en.wikipedia.org/wiki/Quantum_algorithm#Algorithms_based_on_quantum_walks","target":"_blank"}},[_vm._v("quantum walks")]),_vm._v(" is proved to give exponential speedups on some tasks by preparing all computational bases and run through a black box to get the result in parallel. Other quantum algorithms based on "),_c('a',{attrs:{"href":"https://en.wikipedia.org/wiki/Quantum_algorithm#Algorithms_based_on_the_quantum_Fourier_transform","target":"_blank"}},[_vm._v("quantum Fourier transform")]),_vm._v(" also perform exponentially faster than classical computers, among which the "),_c('a',{attrs:{"href":"https://en.wikipedia.org/wiki/Shor%27s_algorithm","target":"_blank"}},[_vm._v("Shor's algorithm")]),_vm._v(" solving the integer factorization problem is famous for its threat to today's widely-used encryption technologies.")]),_c('el-card',{attrs:{"shadow":"always","body-style":{ padding: '10px 20px', background: '#2A5CAA' }}},[_c('p',{staticClass:"interactive-message"},[_vm._v("Interactive Part: change the amount of data qubits and see how many computational bases are there\n\t\t\t\t\t\t\t"),_c('el-button',{staticClass:"interactive-start",attrs:{"type":"primary","icon":_vm.running == 'qubit_amount' ?  'el-icon-loading' : 'none',"disabled":_vm.running != null},on:{"click":function($event){return _vm.start_interactive('qubit_amount')}}},[_vm._v(_vm._s(_vm.running == "qubit_amount" ? "Running" : "Start"))])],1)])],1),_c('div',{directives:[{name:"show",rawName:"v-show",value:(_vm.step == 2),expression:"step == 2"}]},[_c('h1',[_vm._v("Quantum Error Correction")]),_c('p',[_vm._v("Quantum error correction (QEC) is used in quantum computing to protect quantum information from errors due to decoherence and other quantum noise. Unlike classical computing, there's high probability of error per qubit per time slice (about 1e-3) in the process of quantum computation (e.g. noisy quantum gates, noisy quantum preparation, and even noisy measurements).")]),_c('p',[_vm._v("The simplest method of error correction is adding redundancy to qubits, that is to store the qubits information multiple times. When these physical qubit copies disagree in the future, a "),_c('a',{attrs:{"href":"https://en.wikipedia.org/wiki/Quantum_error_correction","target":"_blank"}},[_vm._v("quantum version repetition code")]),_vm._v(" can detect and recover the errors in some circumstances. This is not the same as classical repetition code because direct measurement on each physical qubit will destroy the quantum superposition state. The superposition is the essence of quantum computing supremacy, so a quantum error correction code has to be careful not to destroy the superposition state when doing measurements. Quantum repetition code can only work with single type of Pauli error, so it's not robust to real-world noises composed of all kinds of Pauli errors.")]),_c('p',[_vm._v("QEC can detect and further recover from errors under certain conditions, but it also introduces more noisy gates and noisy ancilla qubits (used to assist measurement). That means QEC does not necessarily reduce the error rate given more and more physical qubits if the gates and ancilla qubits to implement error correction introduce even more errors than it could recover from.")]),_c('h1',[_vm._v("Fault Tolerant Quantum Computing")]),_c('p',[_vm._v("Fault tolerant quantum computing is a step forward from quantum error correction. It requires that given any small error rate requirement $\\epsilon$, we can build a logical qubit with at most $\\epsilon$ error rate with bounded amount of physical qubits. Fault tolerant quantum computing is based on QEC but is more strict in terms of the ability to recover from ubiquitous errors. The fault tolerant quantum computing is still a research front and one need to prove a certain QEC code and it's decoder is fault tolerant mathematically or in experiment.")])]),_c('div',{directives:[{name:"show",rawName:"v-show",value:(_vm.step == 3),expression:"step == 3"}]},[_c('h1',[_vm._v("Topological Code: Surface Code")]),_c('p',[_vm._v("Topological code is a special kind of quantum error correction code that only involves geometrically local gates which means every gate only interacts with adjacent qubits, making it practical to implement. Surface code is one of the topological codes, first proposed as "),_c('a',{attrs:{"href":"https://en.wikipedia.org/wiki/Toric_code","target":"_blank"}},[_vm._v("toric code")]),_vm._v(" in which every qubit is on the surface of a torus. The surface of torus can be mapped onto a 2D square with periodic boundary condition on top, bottom boundary and left, right boundary. Given the current superconducting quantum chip fabricated in 2D, it's hard to implement toric code only with local gates because of the periodic boundary.")]),_c('p',[_vm._v("Planar code is a surface code that doesn't require periodic boundary condition, and thus is the most promising one given the current fabrication procedure of superconducting quantum chip. Early version of planar code is a straightforward step from toric code, but it's proven to be sub-optimal in terms of code distance per qubit. Thus, we use the optimized rotated planar code in our project, as shown below.")]),_c('div',{staticStyle:{"text-align":"center"}},[_c('img',{staticStyle:{"height":"250px"},attrs:{"src":require("@/assets/rotated_planar_code_annotated.svg")}})]),_c('p',[_vm._v("The rotated planar code consists of $d\\times d$ data qubits. We use only odd number $d$ so that the error correction ability on X and Z Pauli errors is balanced. It has $\\frac{(d^2-1)}{2}$ Z stabilizers and $\\frac{(d^2-1)}{2}$ X stabilizers, each measures the adjacent 4 or 2 data qubits, according to whether the stabilizer is on the boundary. A demonstration of $d=3$ rotated planar code is shown above. Each piece of rotated planar code forms a single logical qubit, because there are $d^2-1$ stabilizers each locks down a degree of freedom of a data qubit, leaving $d^2-(d^2-1) = 1$ degree of freedom. The goal of quantum error correction is to recover the logical state from random errors, which means the logical state should not be changed after correction. If a logical operator is introduced after correction, then the error correction fails. Logical Pauli X operator and logical Pauli Z operator is shown above as arrows, meaning that if all the qubits on that arrow has Pauli X or Z error then a logical X or Z operator is introduced respectively. Note that the CX gates and HCXH gates are used to implement the measurement with the help of stabilizer ancilla qubits, each is a quantum gate operating on two qubits. We'll not dig into the concrete implementation of measurement using those gates but just take the result that the joint measurement of the adjacent 4 or 2 qubits is feasible.")]),_c('div',{staticStyle:{"text-align":"center"}},[_c('img',{staticStyle:{"height":"150px"},attrs:{"src":require("@/assets/Stab.png")}})]),_c('p',[_vm._v("Take a Z stabilizer with 4 adjacent data qubits as an example, as show above, it measures $Z_1 Z_2 Z_3 Z_4$ of these data qubits. Suppose at first the measurement result is +1, then if one of the four data qubits has a Pauli X error which bit-flip that data qubit, then the measurement result becomes -1 because $1^3\\times(-1)^1 = -1$. However, if two data qubits or all four data qubits have Pauli X errors, then the measurement result is still +1 given that $1^2\\times(-1)^2 = 1$ and $(-1)^4 = 1$, meaning that this stabilizer is not able to detect those errors.")]),_c('el-card',{attrs:{"shadow":